<template>
<div class="total">
    <div class="form">
        <div>
            <label>제목</label>
            <input id="title" type="text" v-model="qnaTitle" ref="qnaTitle">
        </div>
        <div>
            <label>작성자</label>
            <input id="user" type="text" v-model="qnaUserid" ref="qnaUserid" readonly>
        </div>
        <div>
            <label>내용</label><br>
            <textarea cols="30" rows="10" v-model="qnaContent" ref="qnaContent"></textarea>
        </div>
        <div>
            <input type="button" value="수정" @click="check">
            <input type="button" value="목록" @click="move">
        </div> 
    </div>
</div>
</template>

<script>
import axios from 'axios';

export default {
    data: function () {
        return{
            qnaNo: '',
            qnaTitle: '',
            qnaContent: '',
            qnaUserid: ''
        }
    },
    created(){
        axios.get(`http://localhost:9999/happyhouse/api/qna/${this.$route.query.no}`).then(({data}) => {
            this.qnaNo = data.qnaNo;
            this.qnaTitle = data.qnaTitle;
            this.qnaContent = data.qnaContent;
            this.qnaUserid = data.qnaUserid;
        })
    },
    methods:{
        check(){
            let err = true;
            let msg = '';
            !this.qnaTitle && ((msg = '제목 입력'), (err = false), this.$refs.qnaTitle.focus()); 
            err && !this.qnaContent && ((msg = '내용 입력'), (err = false), this.$refs.qnaContent.focus());

            if(!err)
                alert(msg);
            else
                this.update();
        },
        update(){
            axios.put(`http://localhost:9999/happyhouse/api/qna/${this.qnaNo}`,{
                qnaNo: this.qnaNo,
                qnaTitle: this.qnaTitle,
                qnaContent: this.qnaContent,
                qnaUserid: this.qnaUserid
            })
            .then(({data}) => {
                let msg='수정 중에 문제 발생';
                if (data === 'success') {
                    msg = '수정 완료.';
                }
                alert(msg);
                this.move();
            })
        },
        move(){
            this.$router.push('/');
        }
    }
}
</script>

