<template>
    <div class="total">
        <div v-if="list.length">
            <table>
                <tr>
                    <th>글 번호</th>
                    <th>제목</th>
                    <th>작성자</th>
                    <th>작성 일시</th>
                </tr>

                <tr v-for="info in list" :key="info">
                    <td>{{info.qnaNo}}</td>
                    <td><router-link :to="'read?no=' + info.qnaNo">{{info.qnaTitle}}</router-link></td>
                    <td>{{info.qnaUserid}}</td>
                    <td>{{getDate(info.qnaDatetime)}}</td>
                </tr>

            </table>
        </div>
        <div v-else>
            등록된 QnA가 없습니다.
        </div>  
        <div id="btn">
            <button class="btn btn-primary" @click="move">글쓰기</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';

export default {
    name: 'List',
    data: function(){
        return{
            list:[]
        };
    },
    created(){
        axios.get('http://localhost:9999/happyhouse/api/qna').then(({ data }) => {
            this.list = data;
        });
    },
    methods:{
        getDate(regtime){
            return moment(new Date(regtime)).format('YYYY.MM.DD');
        },
        move(){
            this.$router.push('/create');
        }
    }
}
</script>

<style>
    .total{width: 80%; margin: auto;}
     table{
        border: 1px solid black;
        width: 100%;
        margin: auto;
        border-collapse: collapse;
        text-align: center;
    }
    tr, th, td{
        border: 1px solid black;
    }
    th{background-color: cornflowerblue;}
    #btn{float: right; margin-top: 20px;}
</style>