<template>
  <div>삭제중...</div>
</template>

<script>
import axios from 'axios';

export default {
    created(){
        param = new URL(document.location).searchParams;
        axios.delete(`http://localhost:9999/happyhouse/api/qna/${param.get('no')}`).then(({data}) => {
            let msg = '삭제 중 문제 발생';
            if(data === 'success'){
                msg = '삭제 완료';
            }
            alert(msg);
            this.$router.push('/');
        })
    }
}
</script>

