<template>
    <div class="total">
        <div class="form">
            <div class="insideForm">
                <label>제목</label>
                <input id="title" type="text" v-model="qnaTitle" ref="qnaTitle">
            </div>
            <div class="insideForm">
                <label>작성자</label>
                <input id="user" type="text" v-model="qnaUserid" ref="qnaUserid">
            </div>
            <div class="insideForm">
                <label>내용</label><br>
                <textarea id="content" cols="30" rows="10" v-model="qnaContent" ref="qnaContent"></textarea>
            </div>
            <div>
                <input type="button" value="작성" @click="check">
                <input type="button" value="목록" @click="move">
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data: function () {
        return{
            qnaTitle: '',
            qnaContent: '',
            qnaUserid: ''
        }
    },
    methods:{
        check(){
            let err = true;
            let msg = '';
            !this.qnaTitle && ((msg = '제목 입력'), (err = false), this.$refs.qnaTitle.focus()); 
            err && !this.qnaUserid && ((msg = '작성자 입력'), (err = false), this.$refs.qnaUserid.focus());
            err && !this.qnaContent && ((msg = '내용 입력'), (err = false), this.$refs.qnaContent.focus());

            if(!err)
                alert(msg);
            else
                this.create();
        },
        create(){
            axios.post('http://localhost:9999/happyhouse/api/qna/',{
                qnaTitle: this.qnaTitle,
                qnaContent: this.qnaContent,
                qnaUserid: this.qnaUserid
            })
            .then(({data}) => {
                let msg='추가 중에 문제 발생';
                if (data === 'success') {
                    msg = '추가 완료.';
                }
                alert(msg);
                this.move();
            })
        },
        move(){
            this.$router.push('/');
        }
    }
}
</script>

<style>
    .total{width: 80%; margin: auto;}
    .form{width: 90%; margin: auto;}
    #title{margin-left: 35px}
    #user{margin-left: 19px}
</style>