<template>
<div>
    <table>
        <tr>
            <th>글 번호</th>
            <td>{{info.qnaNo}}</td>
        </tr>
        <tr>
            <th>제목</th>
            <td>{{info.qnaTitle}}</td>
        </tr>
        <tr>
            <th>작성자</th>
            <td>{{info.qnaUserid}}</td>
        </tr>
        <tr>
            <th>작성일</th>
            <td>{{getDate(info.qnaDatetime)}}</td>
        </tr>
        <tr>
            <td colspan="2">{{info.qnaContent}}</td>
        </tr>
    </table>
    <br>
    <div class="text-center">
        <router-link :to="'/update?no=' + info.qnaNo"><button class="btn btn-primary">수정</button></router-link>
        <router-link :to="'/delete?no=' + info.qnaNo"><button class="btn btn-primary">삭제</button></router-link>
        <router-link :to="'/'"><button class="btn btn-primary">목록</button></router-link>
    </div>   
</div>  
</template>

<script>
import axios from 'axios';
import moment from 'moment';

export default {
    data: function () {
        return{
            info: {}
        }
    },
    created(){
        const params = new URL(document.location).searchParams;
        axios.get(`http://localhost:9999/happyhouse/api/qna/${params.get('no')}`).then(({data}) => {
            this.info = data;
        })
    },
    methods:{
        getDate(regtime){
            return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
        }
    }
}
</script>

